import { createContext, useEffect, useState } from 'react';
import { v4 as uuidv4 } from 'uuid';

export const DetailsContext = createContext()

const DetailsContextProvider = (props) => {

  const [details, setDetails] = useState([])

  useEffect(() => {
    setDetails(JSON.parse(localStorage.getItem('details')))
  }, [])

  useEffect(() => {
    localStorage.setItem('details', JSON.stringify(details));
  })



  const sortedDetails = details.sort((a, b) => (a.name < b.name ? -1 : 1));

  const addDetails = (name, cocktail, points) => {
    setDetails([...details, { id: uuidv4(), name, cocktail, points }])
  }

  const deleteDetails = (id) => {
    setDetails(details.filter(detail => detail.id !== id))
  }

  const updateDetails = (id, updatedDetail) => {
    setDetails(details.map((detail) => detail.id === id ? updatedDetail : detail))
  }

  return (
    <DetailsContext.Provider value={{ sortedDetails, addDetails, deleteDetails, updateDetails }}>
      {props.children}
    </DetailsContext.Provider>
  )
}

export default DetailsContextProvider;