import React, { useContext } from 'react';
import { DetailsContext } from '../contexts/DetailsContext';
import "./Detail.css";

function TaskDetail({ details }) {

  const { deleteDetails, editDetails } = useContext(DetailsContext)
  const { id, name, cocktail, points } = details
  return (
    <div className="table-row">
      <div className="row__items">{name}</div>
      <div className="row__items">{cocktail}</div>
      <div className="row__items">{points}</div>
      <div className="row__items">
        <button type="button" onClick={() => editDetails(details)}>Edit</button>
        <button type="button" onClick={() => deleteDetails(id)}>Delete</button>
      </div>
    </div>
  )
}

export default TaskDetail
