import React, { useContext } from 'react';
import Detail from './Detail';
import "./TaskDetails.css";
import { DetailsContext } from '../contexts/DetailsContext';


function TaskDetails() {

  const { sortedDetails } = useContext(DetailsContext);

  return (
    <div className="task-details">
      <h1>Enteries</h1>
      <div className="drinks"></div>
      <div className="table">
        <div className="table-header">
          <div className="header__item"><a id="name" className="filter__link" href="#">Name</a></div>
          <div className="header__item"><a id="wins" className="filter__link" href="#">Cocktail</a></div>
          <div className="header__item"><a id="draws" className="filter__link" href="#">Points Given</a></div>
          <div className="header__item"><a id="losses" className="filter__link" href="#">Action</a></div>
        </div>
        {sortedDetails.map((details) => (
          <div key={details.id}>
            <Detail details={details} />
          </div>
        ))}




      </div>
    </div>
  )
}

export default TaskDetails