import React, { useContext, useState } from 'react';
import { DetailsContext } from '../contexts/DetailsContext';
import "./AddTask.css"

function AddTask() {

  const { addDetails } = useContext(DetailsContext);


  const [details, setDetails] = useState({
    name: "",
    cocktail: "",
    points: ""
  });

  const onInputChange = (e) => {
    setDetails({ ...details, [e.target.name]: e.target.value });
  };

  const { name, cocktail, points } = details;

  const onSubmit = (e) => {
    e.preventDefault();
    addDetails(name, cocktail, points);
  }


  return (
    <form className="container" onSubmit={e => onSubmit(e)}>
      <h2>Add Entry</h2>
      <h4>Name</h4>
      <input type="text" placeholder="Enter your name" name="name" value={name} onChange={(e) => onInputChange(e)} required />
      <h4>Select cocktail</h4>
      <select name="cocktail" value={cocktail} onChange={(e) => onInputChange(e)} required>
        <option value="Malta">Malta</option>
        <option value="Santra">Santra</option>
        <option value="Sonfee">Sonfee</option>
      </select>
      <h4>Points(0 to 10)</h4>
      <input type="number" name="points" id="points" min="0" max="10" value={points} onChange={(e) => onInputChange(e)} required />
      <div className="buttons">
        <button type="reset" id="reset">Reset</button>
        <button type="submit" id="submit">Add</button>
      </div>
    </form>

  )
}

export default AddTask
